/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

h1 {
font-family: 'Arial';
font-style: normal;
font-weight: 700;
font-size: 48px;
line-height: 130%;
}

h3 {
font-family: 'Open Sans';
font-style: normal;
font-weight: 700;
font-size: 22px;
line-height: 130%;
}

h4 {
font-family: 'Open Sans';
font-style: normal;
font-weight: 700;
font-size: 18px;
line-height: 130%;
color: #252C3E;

}

h2 {
  font-family: 'Open Sans';
font-style: normal;
font-weight: 700;
font-size: 26px;
line-height: 130%;
color: #FFFFFF;

}

@media (max-width: 320px) {
  ::-webkit-scrollbar {
    width: 0px;
    background: transparent; 
    }
  
}

.scrollbar-hidden::-webkit-scrollbar {
  display: none;
}


